/* ===============================
   ボタン ベース
=============================== */
.button {
  width: 100%;
  max-width: 22.1875rem;
  font-family: "Viga", sans-serif;
  font-size: 1.25rem;
  color: #222222;
  font-weight: 400;
  line-height: 1;
  display: inline-block;
  text-align: center;
  padding: 1.875rem 0;
  background-color: #ffffff;
  border: 1px solid #222222;
  position: relative;
  z-index: 1;
  transition: 0.3s;
}

.button:after {
  content: "";
  display: inline-block;
  width: 1.25rem;
  height: 1.25rem;
  background-image: url("../../assets/images/common/arrow-right-black.svg");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  position: absolute;
  right: 1.25rem;
  top: 50%;
  transform: translateY(-50%);
  transition: transform 0.3s, background-image 0.3s;
}

/* ===============================
   バリエーション
=============================== */
/* 下層ページ共通のENTRYボタン */
.button.button--entry {
  font-size: 1rem;
  background-color: #AFA86F;
  width: 100%;
  max-width: 34.75rem;
  padding: 1.6875rem 0;
}
@media screen and (min-width: 768px) {
  .button.button--entry {
    max-width: 34.75rem;
  }
}

/* ヘッダーの黒ENTRYボタン */
.button.button--entry.button--entry-black {
  width: 7.875rem;
  font-size: 0.875rem;
  background-color: #222222;
  color: #ffffff;
  padding: 0.875rem 0;
}

/* ヘッダーの黒ENTRYボタンでは矢印非表示 */
.button.button--entry.button--entry-black::after {
  content: none;
}

/* ヘッダーのアクセントカラーENTRYボタン */
.button.button--entry.button--entry-accent {
  width: 7.875rem;
  font-size: 0.875rem;
  background-color: #AFA86F;
  color: #ffffff;
  padding: 0.875rem 0;
}

/* ヘッダーのアクセントカラーENTRYボタンでも矢印非表示 */
.button.button--entry.button--entry-accent::after {
  content: none;
}

/* ===============================
   hover
=============================== */
/* 通常ボタン hover */
.button:hover {
  background-color: #222222;
  color: #ffffff;
}

.button:hover:after {
  background-image: url("../../assets/images/common/arrow-right-white.svg");
}

/* 黒ボタン hover */
.button.button--entry.button--entry-black:hover {
  background-color: #ffffff;
  color: #222222;
}

/* アクセントカラーボタン hover */
.button.button--entry.button--entry-accent:hover {
  background-color: #988C2C;
}

