.staff-interview__job-details {
  margin-top: 1.4375rem;
}

.staff-list__items {
  padding: 0 1.1875rem;
}

@media screen and (min-width: 768px) {
  .users-voice__inner {
    padding: 0 1.375rem;
  }
}

.users-voice__title {
  font-family: "Noto Sans JP", sans-serif;
  font-size: 1.25rem;
  color: #222222;
  font-weight: 700;
  line-height: 1;
}
@media screen and (min-width: 768px) {
  .users-voice__title {
    font-size: 2rem;
  }
}

.users-voice__items {
  display: grid;
  grid-template-columns: 1fr;
  gap: 2.875rem;
  margin-top: 1.4375rem;
}
@media screen and (min-width: 768px) {
  .users-voice__items {
    grid-template-columns: 1fr 1fr;
  }
}

.users-voice__year {
  font-family: "Noto Sans JP", sans-serif;
  font-size: 1rem;
  color: #AFA86F;
  font-weight: 700;
  line-height: 1;
}

.users-voice__name {
  display: block;
  font-family: "Noto Sans JP", sans-serif;
  font-size: 1.125rem;
  color: #222222;
  font-weight: 700;
  line-height: 1;
  margin-top: 0.8125rem;
}
@media screen and (min-width: 768px) {
  .users-voice__name {
    font-size: 1.5rem;
  }
}

.users-voice__text {
  display: block;
  font-family: "Noto Sans JP", sans-serif;
  font-size: 1rem;
  color: #222222;
  font-weight: 400;
  line-height: 1.8;
  margin-top: 1.5625rem;
}

.users-voice__image {
  width: 100%;
  aspect-ratio: 4/3;
  margin-top: 2.25rem;
}
@media screen and (min-width: 768px) {
  .users-voice__image {
    aspect-ratio: 980/560;
  }
}

.users-voice__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}